@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap);
.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 50px;
  width: 80%;
  font-weight: 300;
}

.movies .movies__movie {
  width: 420px;
  margin-bottom: 75px;
  background-color: white;
  padding: 30px;
  box-shadow: 3px 13px 23px 2px rgba(0, 0, 0, 0.14);
}

.movies__movie h3 {
  font-weight: 600;
}
.movies__movie img {
  transform: translateY(-50px);
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movies__movie .movie__data {
  transform: translateY(-30px);
}

.genres .genres__name {
  justify-content: flex-start;
  margin-right: 10px;
}

.genres .genres__genre {
  margin-right: 10px;
}

.genres {
  padding-left: 0px;
  padding-bottom: 30px;
}

.movie__summary {
  transform: translateY(-40px);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: #eff3f7;
}

html,
body,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  display: flex;
  justify-content: center;
  align-items: center;
}

.loader__text {
  font-size: 36px;
}

ol,
ul {
  list-style: none;
  display: flex;
}

